.main {
  padding: 60px 0 0 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.main__title {
  margin: 0;
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  font-size: 40px;
  line-height: 50px;
}

.main__subtitle {
  margin: 0;
  padding: 20px 0 0 0;
  color: #FFFFFF;
  font-family: 'Inter', sans-serif;
  font-weight: 700;
  font-size: 32px;
  line-height: 40px;
  text-align: center;
}

.main__message {
  color: #FFFFFF;
  padding: 18px 0 0 0;
  font-size: 18px;
  line-height: 24px;
  font-family: 'Inter', sans-serif;
  font-weight: 500;
}

@media (max-width: 768px) {
  .main {
    padding: 50px 0 0 0;
  }

 .main__title {
    font-size: 35px;
    line-height: 45px;
  }

  .main__subtitle {
    padding: 15px 0 0 0;
    font-size: 24px;
    line-height: 30px;
  }

  .main__message {
    padding: 14px 0 0 0;
    font-size: 16px;
    line-height: 20px;
  }

  @media (max-width: 480px) {
    .main {
      padding: 40px 0 0 0;
    }

    .main__title {
       font-size: 30px;
       line-height: 40px;
     }

    .main__subtitle {
      padding: 12px 0 0 0;
      font-size: 20px;
      line-height: 26px;
    }

    .main__message {
      padding: 12px 0 0 0;
      font-size: 13px;
      line-height: 18px;
    }
  }

  @media (max-width: 320px) {
    .main {
      padding: 30px 0 0 0;
    }

    .main__title {
       font-size: 26px;
       line-height: 36px;
     }

    .main__subtitle {
      padding: 10px 0 0 0;
      font-size: 18px;
      line-height: 24px;
    }

    .main__message {
      padding: 8px 0 0 0;
      font-size: 12px;
    }
  }
}